@charset "UTF-8";
:root {
  --background-color-transparent: rgba(19, 19, 22, 0.79);
  --font-body-color: #ffefcd;
  --font-color-second: #424539;
  --font-color-title: rgb(136, 192, 255);
  --font-color-active: rgb(119, 150, 243);
  --font-color-not-active: rgba(139, 176, 255, 0.424);
  --background-color: rgb(32, 33, 45);
  --background-second: rgb(2, 10, 16);
  --background-selected: rgba(43, 45, 65, 0.337);
  --selected-color: rgb(113, 203, 255);
  --themes-color: rgb(94, 77, 168);
  --background-thumb: rgb(22, 22, 30);
  --scrollbar-thumb-bgcolor: rgb(71, 83, 45);
  --border-color: rgba(60, 84, 141, 0.286);
  --color-active: rgb(4, 217, 255);
  --pos: center center;
  --size: 0;
  --inset-top: 0;
  --inset-right: 100%;
  --inset-bottom: 100%;
  --inset-left: 0;
  --stripe-width: 10px;
  --nav-marker-border: rgb(59, 134, 134);
  --sidebar-width: 28.5rem;
  --duration: 0.3s;
  --button-size: 4rem;
  --mob-expanded-width: 320px;
  --mob-collapsed-width: 4rem;
}

:root {
  --swiper-scrollbar-top: auto;
  --swiper-scrollbar-bg-color: rgba(0, 0, 0, 0.1);
  --swiper-scrollbar-drag-bg-color: rgba(0, 0, 0, 0.5);
  --swiper-scrollbar-size: 0.4rem;
  --swiper-scrollbar-bottom: 0.4rem;
  --swiper-scrollbar-sides-offset: 1%;
  --swiper-scrollbar-border-radius: 1rem;
  --swiper-theme-color: $themeColor;
  --swiper-navigation-size: 3rem;
  --swiper-pagination-color: #f65e38;
  --swiper-pagination-bullet-opacity: 1;
  --swiper-pagination-bullet-size: 1.2rem;
  --swiper-pagination-bullet-border-radius: 50%;
  --swiper-pagination-bullet-inactive-opacity: 0.4;
  --swiper-pagination-bullet-inactive-color: rgb(0, 0, 0);
  --swiper-pagination-bullet-width: 1.2rem;
  --swiper-pagination-bullet-height: 1.2rem;
  --swiper-pagination-top: auto;
  --swiper-pagination-bottom: 0.5rem;
  --swiper-pagination-bullet-horizontal-gap: 0.5rem;
  --swiper-transition-opacity: 0.9s;
}

.box-shadow {
  box-shadow: 0 1px 10px #020101;
  backdrop-filter: blur(22px);
}

@media (min-width: 490.98px) and (max-width: 1920px) {
  .font-size-calc-h2 {
    font-size: calc(28px + 22 * (100vw - 490px) / 1430);
  }
}

.top-box {
  align-items: center;
}
@media (min-width: 960.99px) {
  .top-box {
    min-height: 100vh;
  }
}

.bg-image {
  position: absolute;
  top: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  pointer-events: none;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
}
.bg-image__image {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 100%;
}
.bg-image__image img {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  object-fit: cover;
  object-position: top center;
  will-change: transform;
  filter: grayscale(50%);
}

.bg-bottom {
  z-index: 9;
  width: 100%;
  height: 45%;
  position: absolute;
  grid-area: 1/-1;
  bottom: 0;
  transform: translateY(40px);
  background-image: linear-gradient(transparent, #ffefcd 80%);
  pointer-events: none;
}

.gradient-header {
  background-image: linear-gradient(-45deg, rgb(2, 1, 1), rgba(61, 61, 61, 0.514) 65%, rgb(2, 1, 1) 75%);
}

.gradient-footer {
  background-image: linear-gradient(-40deg, rgb(2, 1, 1) 35%, rgba(199, 199, 199, 0.055) 65%, rgb(2, 1, 1) 75%);
}

.gradient-border-bottom {
  background-image: linear-gradient(to left top, rgb(52, 62, 85) 25%, transparent 75%);
}

.gradient-services {
  background-image: linear-gradient(to right, #050202 20%, #1c153f 50%, #050202 80%);
}

.icon-style {
  display: flex;
  align-items: center;
  height: 100%;
  padding: 0 1.5rem;
  border: 1px var(--border-color);
  border-style: none solid;
}

.text-shadow {
  color: #ffffff;
  text-shadow: 0 0 15px #876b53, 0 0 1em #876b53, 0 0 0.2em rgba(81, 79, 252, 0.589);
}

.link_split {
  position: relative;
  outline: 0;
  line-height: 1;
  transition: color 0s 0.5s;
  display: inline-block;
  white-space: nowrap;
}
.link_split:hover {
  text-shadow: none;
  color: transparent;
  transition: none;
}
.link_split:hover::before {
  transform: translate3d(0px, -2px, 0);
}
.link_split:hover::after {
  transform: translate3d(0px, 2px, 0);
}
.link_split:hover::before, .link_split:hover::after {
  color: #e09132;
  transition: color 0.5s, transform 0.5s;
}
.link_split::before, .link_split::after {
  content: attr(data-letters);
  position: absolute;
  top: 0;
  left: 0;
  color: #fff;
  transition: color 0.5s, transform 0.5s;
}
.link_split::before {
  clip-path: polygon(0 0, 100% 0%, 100% 30%, 0 65%);
}
.link_split::after {
  clip-path: polygon(0 65%, 100% 30%, 100% 100%, 0 100%);
}

.link_underline-center {
  position: relative;
  transition: color 0.3s ease-in-out;
}
.link_underline-center::before {
  content: "";
  transform: scaleX(0);
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 1px;
  transition: transform 0.3s ease-in-out;
  opacity: 0.6;
  background-color: #e09132;
}
.link_underline-center::after {
  bottom: 0;
  left: 0;
}
.link_underline-center:hover::before {
  transform: scaleX(1);
}
.link_underline-center:hover a {
  opacity: 1;
}

.link_underline::before, .link_underline::after {
  position: absolute;
  width: 0;
  height: 1px;
  content: "";
  transition: width 0.4s ease-in-out;
  opacity: 0.6;
  background-color: #e09132;
}
.link_underline::before {
  right: 0;
  bottom: 0;
}
.link_underline::after {
  bottom: 0;
  left: 0;
}
.link_underline._active::before {
  width: 45%;
}
.link_underline._active::after {
  width: 45%;
}
.link_underline:hover {
  transition: color 0.4s ease-in-out;
  color: #e09132;
}
.link_underline:hover::before {
  width: 45%;
}
.link_underline:hover::after {
  width: 45%;
}
.link_underline:hover a {
  opacity: 1;
}

.is-open {
  display: grid;
  width: 100%;
  transition: grid-template-rows 0.4s ease-in-out;
  grid-template-columns: 1fr;
  grid-template-rows: 0fr;
}
.is-open._is-open {
  grid-template-rows: 1fr;
}

.transform-perspective {
  transform-style: preserve-3d;
  backface-visibility: hidden;
  perspective: 100px;
}
.transform-perspective .transform-box {
  transition: all 0.8s cubic-bezier(0.77, 0, 0.175, 1);
}
.transform-perspective .transform-box:hover {
  opacity: 0.3 !important;
  transform-origin: 50% -100%;
  transform: rotateX(-16deg) translateZ(-22px);
}

.grad_underline {
  /* Определяем фоновые изображения */
  transition: background-size 400ms;
  /* Размер фона для каждого слоя */
  background-image: linear-gradient(to left, transparent, transparent), linear-gradient(to left, var(--background-selected), var(--font-color-active));
  /* Позиционирование фона */
  background-repeat: no-repeat;
  /* Повторение фона */
  background-position: 100% 100%, 0 100%; /* Второе изображение */
  /* Переход для изменения размера фона */
  background-size: 100% 1px, 0 1px; /* Второе изображение */
}

.border-present {
  position: absolute;
  display: block;
  color: #e09132;
  background-image: linear-gradient(to right, currentColor 0%, #e09132 8%, transparent 8%, transparent 92%, #e09132 92%, currentColor 100%), linear-gradient(to right, currentColor 0%, #e09132 8%, transparent 8%, transparent 92%, #e09132 92%, currentColor 100%), linear-gradient(to bottom, currentColor 0%, #e09132 11%, transparent 11%, transparent 89%, #e09132 89%, currentColor 100%), linear-gradient(to bottom, currentColor 0%, #e09132 11%, transparent 11%, transparent 89%, #e09132 89%, currentColor 100%);
  background-repeat: no-repeat;
  background-position: 0 0, 0 100%, 0 0, 100% 0;
  background-size: 100% 1px, 100% 1px, 1px 100%, 1px 100%;
  inset: 0.7rem;
}

.pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin-top: 6rem;
}
.pagination .swiper-pagination-bullet {
  width: 15px;
  height: 15px;
  content: "";
  border-radius: 50%;
  background: #2a3514;
  transition: background 0.5s ease-in-out;
}
.pagination .swiper-pagination-bullet:not(:last-child) {
  margin-right: 2rem;
}
.pagination .swiper-pagination-bullet-active {
  width: 15px;
  height: 15px;
  content: "";
  border-radius: 50%;
  background: #9acd32;
  scale: 1.2;
  transition: scale 0.5s ease-in-out;
}
.pagination.swiper-pagination-lock {
  display: none;
}

@media (min-width: 920.99px) {
  /* FLOW: RIGHT */
  [data-tooltip] {
    position: relative;
  }
  [data-tooltip]::after {
    content: attr(data-tooltip);
    border-radius: 0.6rem;
    border: 1px solid var(--border-color);
    background-color: rgba(35, 52, 59, 0.8705882353);
    position: absolute;
    padding: 1.2rem;
    color: #fff;
    opacity: 0;
    visibility: hidden;
    transition: all 0.4s ease-in-out;
    font-family: "Source Sans", sans-serif;
    font-weight: 200;
    font-size: 1.3rem;
    letter-spacing: 0.2em;
    text-transform: uppercase;
    z-index: 9;
    white-space: nowrap;
  }
  [data-tooltip]::before {
    content: "";
    border: 9px solid transparent;
    border-right: 9px solid var(--font-color-active);
    position: absolute;
    z-index: 9;
    margin-left: -20px;
    bottom: 12%;
    opacity: 0;
    visibility: hidden;
    transition: all 0.4s ease-in-out;
  }
  [data-tooltip]:hover::before,
  [data-tooltip]:hover::after {
    visibility: visible;
    opacity: 1;
    display: block;
  }
  [data-tooltip=""]::before,
  [data-tooltip=""]::after {
    display: none !important;
  }
  [data-tooltip][flow^=right]::after {
    top: 50%;
    left: calc(120rem + 10px);
    transform: translate(0.5em, -50%);
  }
  [data-tooltip][flow^=right]::before {
    top: 50%;
    left: 121rem;
    transform: translate(0.5em, -50%);
  }
  [data-tooltip][flow^=right]:hover::before,
  [data-tooltip][flow^=right]:hover::after {
    animation: data-tooltip 300ms ease-out forwards;
  }
  @keyframes data-tooltip {
    to {
      opacity: 1;
      transform: translate(-110rem, -50%);
    }
  }
}
.breadcrumb a:after {
  content: "";
  border-top: 12px solid transparent;
  border-bottom: 12px solid transparent;
  border-left: 12px solid #e7e9eb;
  position: absolute;
  right: -12px;
  top: 0;
  z-index: 1;
}

html {
  font-style: normal;
  font-size: 62.5%;
  font-weight: 400;
  font-family: "Source Sans", sans-serif;
  height: 100%;
  counter-reset: heading;
  letter-spacing: 1.1px;
  color: var(--font-body-color);
}
@media screen and (max-width: 1024px) {
  html {
    font-size: 50%;
  }
}

body {
  font: 1.6rem/1.6 "Source Sans", sans-serif;
  background: #ffefcd;
}
body.no-scroll {
  overflow: hidden;
}
@media screen and (max-width: 490px) {
  body {
    font: 1.5rem/1.5 "Source Sans", sans-serif;
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: none;
  scrollbar-width: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img,
svg {
  vertical-align: top;
  user-select: none;
  pointer-events: none;
  width: 100%;
  height: auto;
  max-width: 100%;
}

a {
  cursor: pointer;
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: none;
  vertical-align: baseline;
  background-color: transparent;
}

a:focus {
  text-decoration: none;
}

a:visited {
  vertical-align: middle;
  text-emphasis-style: none;
}

h2,
h3 {
  font-family: "Amatic SC", sans-serif;
}

input,
button,
textarea {
  border: none;
}

button,
input[type=submit] {
  display: inline-block;
  cursor: pointer;
  background: transparent none;
  box-shadow: none;
}

input,
textarea,
button,
select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

input:focus,
input:active,
button:focus,
button:active {
  outline: none;
}

input,
textarea,
button,
select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

label {
  cursor: pointer;
}

a:focus,
a:active,
input:focus,
textarea:focus {
  outline: none;
}

ul,
ol,
li {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-style: inherit;
}

::selection {
  color: rgb(155, 153, 153);
  background-color: rgba(0, 84, 99, 0.247);
  text-shadow: 0 1px 0 #000;
}

::-moz-selection {
  color: rgb(155, 153, 153);
  background-color: rgba(0, 84, 99, 0.247);
  text-shadow: 0 1px 0 #000;
}

::-webkit-scrollbar {
  width: 0.5rem;
  padding: 0.031em;
}

::-webkit-scrollbar-track {
  background: var(--scrollbar-thumb-bgcolor);
}

::-webkit-scrollbar-thumb {
  border: 1px solid var(--border-color);
  border-radius: 2px;
  background-color: var(--themes-color);
  background-image: -webkit-linear-gradient(45deg, var(--background-thumb) 25%, transparent 25%, transparent 50%, var(--background-thumb) 50%, var(--background-thumb) 75%, transparent 75%, transparent);
}

::-webkit-scrollbar-thumb:hover {
  background-color: var(--background-thumb);
  background-image: -webkit-linear-gradient(45deg, var(--themes-color) 25%, transparent 25%, transparent 50%, var(--themes-color) 50%, var(--themes-color) 75%, transparent 75%, transparent);
}

@font-face {
  font-family: "icon-set";
  font-display: swap;
  src: url("../fonts/icon-set.eot") format("eot"), url("../fonts/icon-set.woff") format("woff"), url("../fonts/icon-set.woff2") format("woff2"), url("../fonts/icon-set.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
.vertical-reverse {
  height: 100vh;
  transform: translateY(-10px);
  z-index: 9;
}

.perform {
  z-index: 11;
  height: calc(100vh - 36rem);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 18rem;
  padding-bottom: 18rem;
}
@media screen and (max-width: 960px) {
  .perform {
    height: 100%;
    padding-top: 14rem;
    padding-bottom: 14rem;
  }
}
.perform__body {
  position: relative;
  display: flex;
  align-items: center;
}
.perform__content {
  width: 100%;
  box-sizing: border-box;
  display: flex;
  gap: 1.6rem;
  backdrop-filter: blur(10px);
}
@media (max-width: 620px) {
  .perform__content {
    flex-direction: column;
  }
}
.perform__column {
  flex: 0 1 50%;
  padding-top: 4rem;
  overflow: hidden;
  display: flex;
}
.perform__column:nth-child(1) {
  flex: 0 1 auto;
  flex-direction: column;
  padding-top: 6.5rem;
}
@media (max-width: 620px) {
  .perform__column:nth-child(1) {
    padding-top: 4rem;
  }
}
.perform__column:nth-child(2) {
  flex-direction: column;
  justify-content: center;
  border-radius: 0.4rem;
  min-width: 260px;
}
.perform__column:nth-child(2) .perform__items {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media screen and (max-width: 960px) {
  .perform__column:nth-child(2) {
    flex: 0 1 40%;
  }
}
.perform__items {
  padding: 2rem 0.5rem;
}
.perform__item {
  filter: contrast(190%);
}
.perform__text {
  position: relative;
  color: #ffefcd;
  border-radius: 2rem;
}
.perform__text .t-01 {
  filter: contrast(190%);
}
.perform__float-left {
  position: relative;
  height: 100%;
}
.perform__float-left.hide {
  float: left;
  shape-outside: polygon(0px 28px, 0px 100%, 30% 100%);
  width: 90%;
  opacity: 0;
}

.hgroup-items h1 {
  line-height: 100%;
  text-transform: uppercase;
  margin-bottom: 2rem;
}
.hgroup-items h2 {
  line-height: 100%;
  margin-bottom: 2.5rem;
  text-transform: uppercase;
}
.hgroup-items p {
  line-height: 135%; /* 25/16 */
}
.hgroup-items p:not(:last-child) {
